import "./main.css";
import mens from "../../../images/image_2024-05-28_16-22-05.png";
import community from "../../../images/icon/community.1119b3fe.svg";
import economy from "../../../images/icon/economy.ad5e136c.svg";
import fuctional from "../../../images/icon/functional.e9016d5e.svg";
import opportunities from "../../../images/icon/opportunities.69cd734d.svg";
import roleplay from "../../../images/icon/roleplay.ff97dda3.svg";
import { Navbar } from "../../Modules/navigation";
import { Footer } from "../../Modules/footer";
import { Link } from "react-router-dom";

const MainPage = () => {
  return (
    <div>
      <Navbar />
      <div className="mainPage">
        <div className="containerHeader">
          <div className="containerHeaderContent">
            <div className="leftPartContainer">
              <h1>
                <span>Region Project</span> — это первый и единственный игровой
                проект в КРМП с уникальной атмосферой, строгими правилами,
                прекрасной игровой аудиторией и с режимом ролевой отыгровки
              </h1>

              <Link to="/start">
                <button className="btnHTSHeader">Как начать играть?</button>
              </Link>
            </div>
            <div className="mensContainer">
              <img className="mensIcon" src={mens} alt="" />
            </div>
          </div>
        </div>

        <div className="containerRecomended">
          <div className="containerContent">
            <h3>Почему выбирают именно нас?</h3>

            <div className="containerStars">
              <div className="blockStar">
                <img src={community} alt="" />
                <p>Уникальный игровой опыт!</p>
              </div>
              <div className="blockStar">
                <img src={economy} alt="" />
                <p>Уникальный игровой опыт!</p>
              </div>
              <div className="blockStar">
                <img src={fuctional} alt="" />
                <p>Уникальный игровой опыт!</p>
              </div>
              <div className="blockStar lastStar">
                <img src={opportunities} alt="" />
                <p>Уникальный игровой опыт!</p>
              </div>
              <div className="blockStar lestStar">
                <img src={roleplay} alt="" />
                <p>Уникальный игровой опыт!</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default MainPage;
